      <b-modal id="no-image-message-modal" hide-footer>
        <div class="d-block text-center">
          <h3>Please upload an image!</h3>
        </div>
        <b-button
          class="mt-3"
          block
          @click="$bvModal.hide('no-image-message-modal')"
